<template><section><template>
  <h2><a href="#InputNumber">InputNumber 计数器</a></h2>
  <div class="introduce-block">
    <z-inputnumber :initial-value="5" :min="1" :max="9" @value-change="getValue"></z-inputnumber>
  </div>
</template>
<pre><code class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">z-inputnumber</span>
<span class="hljs-attr">:initial-value</span>=<span class="hljs-string">"5"</span>
<span class="hljs-attr">:min</span>=<span class="hljs-string">"1"</span>
<span class="hljs-attr">:max</span>=<span class="hljs-string">"9"</span>
<span class="hljs-attr">@value-change</span>=<span class="hljs-string">"getValue"</span>
&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">z-inputnumber</span>&gt;</span>
</code></pre>
<p>当数值发生改变时，计数器会把新的数值传递出来，父组件可以通过<code>@value-change</code>来接收。</p>
<pre><code class="language-js">methods: {
  <span class="hljs-attr">getValue</span>: <span class="hljs-function"><span class="hljs-keyword">function</span>(<span class="hljs-params">val</span>) </span>{
    <span class="hljs-built_in">console</span>.log(val)
  }
}
</code></pre>
<template>
  <div class="introduce-block">
    <z-inputnumber :initial-value="50" :min="1" :max="99" :disabled="true"></z-inputnumber>
  </div>
</template>
<pre><code class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">z-inputnumber</span>
<span class="hljs-attr">:initial-value</span>=<span class="hljs-string">"50"</span>
<span class="hljs-attr">:min</span>=<span class="hljs-string">"1"</span>
<span class="hljs-attr">:max</span>=<span class="hljs-string">"99"</span>
<span class="hljs-attr">:disabled</span>=<span class="hljs-string">"true"</span>
&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">z-inputnumber</span>&gt;</span>
</code></pre>
<template>
  <div class="introduce-block">
    <z-table :ths="['参数','类型','必填','默认值','说明']" :trs="[
            ['initialValue','Number','否','0','初始值'],
            ['min','Number','否','0','最小值'],
            ['max','Number','否','Infinity','最大值'],
            ['disabled','Boolean','否','false','是否禁用计数器']
          ]">
    </z-table>
  </div>
</template>

</section></template>

<script>
export default {
  methods: {
    getValue: function(val) {
      console.log(val)
    }
  }
}
</script>


