<template><section><template>
  <h2><a href="#aside">Aside 侧边栏</a></h2>
  <div class="introduce">
    <z-button :text="'Left'" @click.native="asideLeft.show = true">
    </z-button>
    <z-aside :show="asideLeft.show" :title="'标题'" :type="'left'" @close-aside="asideLeft.show = false">
      <h3>我是Aside-Left</h3>
      <div>组件内部可以自定义HTML结构</div>
      <p>直接在&lt;aside&gt;标签中写即可</p>
      <div>内容长度无限制，超过部分可以滚动浏览</div>
    </z-aside>
  </div>
  <div class="introduce">
    <z-button :text="'Top'" @click.native="asideTop.show = true">
    </z-button>
    <z-aside :show="asideTop.show" :title="'标题'" :type="'top'" @close-aside="asideTop.show = false">
      <h3>我是Aside-Top</h3>
      <div>组件内部可以自定义HTML结构</div>
      <p>直接在&lt;aside&gt;标签中写即可</p>
      <div>内容长度无限制，超过部分可以滚动浏览</div>
    </z-aside>
  </div>
  <div class="introduce">
    <z-button :text="'Right'" @click.native="asideRight.show = true">
    </z-button>
    <z-aside :show="asideRight.show" :title="'标题'" :type="'right'" @close-aside="asideRight.show = false">
      <h3>我是Aside-Right</h3>
      <div>组件内部可以自定义HTML结构</div>
      <p>直接在&lt;aside&gt;标签中写即可</p>
      <div>内容长度无限制，超过部分可以滚动浏览</div>
    </z-aside>
  </div>
  <div class="introduce">
    <z-button :text="'Bottom'" @click.native="asideBottom.show = true">
    </z-button>
    <z-aside :show.sync="asideBottom.show" :title="'标题'" :type="'bottom'" @close-aside="asideBottom.show = false">
      <h3>我是Aside-Bottom</h3>
      <div>组件内部可以自定义HTML结构</div>
      <p>直接在&lt;aside&gt;标签中写即可</p>
      <div>内容长度无限制，超过部分可以滚动浏览</div>
    </z-aside>
  </div>
  <div class="introduce-block">
    <p>备注：组件标签内可以自定义HTML结构。</p>
    <p>组件标签须写上<code class="z-code">@close-aside="xxx.show=false"</code>来通知父组件改变<code class="z-code">show</code>值。</p>
    <z-table :ths="['参数','类型','必填','默认值','说明']" :trs="[
            ['show','Boolean','是','-','显示状态'],
            ['title','String','否','-','表体的数据'],
            ['type','String','否','left','显示位置(left,top,right,bottom)'],
            ['close','String','否','关闭','关闭按钮文字']
          ]">
    </z-table>
  </div>
</template>

<pre><code class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">z-button</span>
<span class="hljs-attr">:text</span>=<span class="hljs-string">"'Left'"</span>
<span class="hljs-attr">@click.native</span>=<span class="hljs-string">"asideLeft.show = true"</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">z-button</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">z-aside</span>
<span class="hljs-attr">:show</span>=<span class="hljs-string">"asideLeft.show"</span>
<span class="hljs-attr">:title</span>=<span class="hljs-string">"'标题'"</span>
<span class="hljs-attr">:type</span>=<span class="hljs-string">"'left'"</span>
<span class="hljs-attr">@close-aside</span>=<span class="hljs-string">"asideLeft.show = false"</span>&gt;</span>
  <span class="hljs-comment">&lt;!-- 自定义内容 --&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">z-aside</span>&gt;</span>
</code></pre>
</section></template>

<script>
export default {
  data() {
    return {
      asideLeft: {
        show: false
      },
      asideTop: {
        show: false
      },
      asideRight: {
        show: false
      },
      asideBottom: {
        show: false
      }
    }
  }
}
</script>


