<template><section><template>
  <h2><a href="#pagination">Pagination 分页器</a></h2>
  <div class="introduce-block">
    <z-pagination :total="11" :page-size="1"></z-pagination>
  </div>
</template>
<pre><code class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">z-pagination</span>
<span class="hljs-attr">:total</span>=<span class="hljs-string">"11"</span>
<span class="hljs-attr">:page-size</span>=<span class="hljs-string">"1"</span>
&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">z-pagination</span>&gt;</span>
</code></pre>
<template>
  <div class="introduce-block">
    <z-pagination :total="280" :page="14" :is-jumper="true" @current-page="getPage"></z-pagination>
  </div>
</template>
<pre><code class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">z-pagination</span>
<span class="hljs-attr">:total</span>=<span class="hljs-string">"280"</span>
<span class="hljs-attr">:page</span>=<span class="hljs-string">"14"</span>
<span class="hljs-attr">:is-jumper</span>=<span class="hljs-string">"true"</span>
<span class="hljs-attr">@current-page</span>=<span class="hljs-string">"getPage"</span>
&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">z-pagination</span>&gt;</span>
</code></pre>
<p>当页数发生改变时，分页器会把新的页码传递出来，父组件可以通过<code>@current-page</code>来接收。</p>
<pre><code class="language-js">methods: {
  <span class="hljs-attr">getPage</span>: <span class="hljs-function"><span class="hljs-keyword">function</span>(<span class="hljs-params">val</span>) </span>{
    <span class="hljs-built_in">console</span>.log(<span class="hljs-string">"当前第"</span> + val + <span class="hljs-string">"页"</span>);
  }
}
</code></pre>
<template>
  <div class="introduce-block">
    <z-pagination :total="29" :page-size="4" :page="8"></z-pagination>
  </div>
</template>
<p>注: 在微信浏览器中使用勿忘加上<code>&lt;meta name=&quot;format-detection&quot; content=&quot;telephone=no&quot;&gt;</code>，不然遇到连续的数字会愚蠢的提示你要不要打电话。</p>
<pre><code class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">z-pagination</span>
<span class="hljs-attr">:total</span>=<span class="hljs-string">"29"</span>
<span class="hljs-attr">:page-size</span>=<span class="hljs-string">"4"</span>
<span class="hljs-attr">:page</span>=<span class="hljs-string">"8"</span>
&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">z-pagination</span>&gt;</span>
</code></pre>
<template>
  <div class="introduce-block">
    <z-table :ths="['参数','类型','必填','默认值','说明']" :trs="[
            ['total','Number','是','-','总条目数'],
            ['pageSize','Number','否','10','每页显示的条目数'],
            ['page','Number','否','1','初始化直接前往某一页'],
            ['isJumper','Boolean','否','false','是否开启页数输入跳转(按回车键前往)']
          ]">
    </z-table>
  </div>
</template>

</section></template>

<script>
import zPagination from 'src/components/Pagination'
import zTable from 'src/components/Table'

export default {
  components: {
    zPagination,
    zTable
  },
  methods: {
    getPage: function(val) {
      console.log("当前第" + val + "页");
    }
  }
}
</script>


