<template><section><template>
  <h2><a href="#backtop">BackTop 返回顶部</a></h2>
  <div class="introduce-block">
    <z-backtop :scroll-top="100"></z-backtop>
  </div>
</template>
<pre><code class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">z-backtop</span>
<span class="hljs-attr">:scroll-top</span>=<span class="hljs-string">"100"</span>
&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">z-backtop</span>&gt;</span>
</code></pre>
<p>效果见本页返回顶部按钮。</p>
<p>按钮距离页面边缘的距离建议使用css来调整，可以根据不同尺寸的设备来设置显示的位置。</p>
<p>默认图标引用方式:</p>
<p><code>&lt;link rel=&quot;stylesheet&quot; href=&quot;//at.alicdn.com/t/font_jplna4shxcfbhuxr.css&quot;&gt;</code></p>
<p><code>&lt;z-backtop&gt;</code>标签中可以自定义html结构，用于替换成自己的返回顶部按钮样式。</p>
<p>例子：</p>
<template>
  <z-backtop class="example" :acceleration="0.2" :stime="5">
  返回顶部
  </z-backtop>
</template>
<pre><code class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">z-backtop</span>
<span class="hljs-attr">:acceleration</span>=<span class="hljs-string">"0.2"</span>
<span class="hljs-attr">:stime</span>=<span class="hljs-string">"5"</span>
&gt;</span>
返回顶部
<span class="hljs-tag">&lt;/<span class="hljs-name">z-backtop</span>&gt;</span>
</code></pre>
<template>
  <div class="introduce-block">
    <z-table :ths="['参数','类型','必填','默认值','说明']" :trs="[
            ['scrollTop','Number','否','0','页面滚动多少距离开始显示返回顶部按钮'],
            ['acceleration','Number','否','0.5','滚动的速度'],
            ['stime','Number','否','10','每次滚动的时间间隔']
          ]">
    </z-table>
  </div>
</template>


</section></template>
<style>
.z-backtop-wrapper.show-top.example {
  position: static;
}
</style>
<script>
import zBacktop from 'src/components/BackTop'
import zTable from 'src/components/Table'

export default {
  components: {
    zBacktop,
    zTable
  }
}
</script>


